export function hitTemplate(hit) {
  return `
    <div>
      <article>
        
        <div class="post-content">
          
          <h2 class="entry-title">
            <a href="${hit.objectID}" rel="bookmark">
            ${hit.objectID}
            </a>
          </h2>
          <div class="post-excerpt">
            ${hit.text}
          </div>
          
          
        </div>
      </article>
    </div>`;
}
